___CSS_LOADER_EXPORT___.push([module.id, `* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Quicksand", sans-serif;
  font-size: 12px;
  background: rgb(255,255,255);
  background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(182,255,245,1) 50%, rgba(255,255,255,1) 100%);
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

main {
  flex: 1;
}

.skip-link {
  position: absolute;
	top: -100px;
	left: 0;
	background-color: #bf1722;
	color: white;
	padding: 10px;
	font-size: 20px;
	z-index: 100;
	text-decoration: none;
}

.skip-link:focus {
  top: 100px;
}

/* sidebar navbar section */
.hamburger {
  display: block;
  position: absolute;
  top: 3px;
  right: 20px;
  cursor: pointer;
  z-index: 2;
  background-color: transparent;
  border: none;
  min-width: 44px;
  min-height: 44px;
}

.hamburger1,
.hamburger2,
.hamburger3 {
  width: 30px;
  height: 4px;
  background-color: #333;
  margin: 6px 0;
  transition: 0.4s;
  border-radius: 5px;
}

.change .hamburger1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 6px);
  transform: rotate(-45deg) translate(-9px, 6px);
  background-color: #bf1722;
}

.change .hamburger2 {
  opacity: 0;
  background-color: #bf1722;
}

.change .hamburger3 {
  -webkit-transform: rotate(45deg) translate(-8px, -8px);
  transform: rotate(45deg) translate(-8px, -8px);
  background-color: #bf1722;
}


.restonav {
  padding: 10px 20px;
  box-shadow: 1px 2px 10px 2px rgba(0, 0, 0, 0.1);
  background: linear-gradient(130deg, rgba(209,255,251,1) 13%, rgba(255,255,255,1) 50%, rgba(210,255,251,1) 75%);
  z-index: 99;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
}

.resto-name a {
  font-family: "Labrada", sans-serif;
  font-size: 1.5rem;
  text-decoration: none;
  font-weight: 550;
  color: #333333;
  letter-spacing: 2px;
  padding: 0.5rem 1.5rem;
}

.sidebar-container {
  position: fixed;
  top: 0;
  bottom: 0;
  right: -300px;
  width: 300px;
  background: linear-gradient(130deg, rgba(230,255,253,1) 23%, rgba(255,251,251,1) 50%, rgba(239,255,253,1) 100%);
  padding: 16px;
  overflow-y: auto;
  transition: all 0.5 ease-in-out;
  display: grid;
  flex-direction: column;
  gap: 10px;
  z-index: 1;
}

.sidebar-container.open {
  right: 0;
}

.sidebar-container ul {
  list-style: none;
  padding: 0;
}

.sidebar-container ul li {
  margin-bottom: 10px;
}

.sidebar-container ul li a {
  text-decoration: none;
  font-size: 1.1rem;
  font-weight: 500;
  color: #333333;
  padding: 10px 15px;
  border-radius: 999px;
  display: inline-block;
  width: 100%;
  min-width: 44px;
  min-height: 44px;
}

.sidebar-container ul li:hover a {
  font-weight: bold;
  color: black;
}

/*homeviews style*/
.homeresto {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 380px;
  width: 100%;
  text-align: center;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  background-position: center;
  background-color: green;
  position: relative;
}

.homeresto:after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(239, 231, 230, 0.3);
}

.homeresto-title {
  font-size: 20em; 
  font-weight: 750;
  color: rgba(255, 255, 255, 0.7);
  font-family: serif;
  z-index: 1;
  padding: 20px 25px;
}

.homeresto-description {
  font-size: 2em;
  color: rgba(255, 255, 255, 0.7);
  font-family: serif;
  z-index: 1;
  padding: 0 25px;
  margin-top: -20px;
  text-transform: uppercase;
  text-decoration: underline;
}

.homeresto-list-tile {
  margin: 0 auto;
  min-height: 100%;
  background-color:  rgba(182, 255, 245, 0.7);
  color: #333;
  font-family: "Labrada", sans-serif;
  font-size: 30px;
  text-align: center;
  position: relative;
  padding: 0 auto;
  margin-bottom: -1.5em;
  border: 2px solid #333;
  border-radius: 10px;
  border-bottom: none;
  z-index: 3;
}

.restaurant-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 15px;
  padding: 2em;
  margin-top: 2em;
}


/* resto list */
.restocard {
  width: 100%;
  background: #fffaf0;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
  border-radius: 12px;
  overflow: hidden;
  transition: transform 0.3s;
}

.restocard:hover {
  transform: scale(1.02);
}

.restohead {
  position: relative;
}

.resto-img {
  width: 100%;
  height: 250px;
  object-fit: cover;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.restohead-rating {
  position: absolute;
  top: 6px;
  left: 5px;
  background-color: rgba(0, 0, 0, 0.7);
  color: #fff;
  padding: 6px 12px;
  border-radius: 8px;
  font-size: 0.9rem;
}

.restohead-score {
  margin-left: 8px;
  font-weight: bold;
}

.restocontent {
  padding: 20px;
  background: #f0f8ff;
}

.restocontent h3 {
  margin: 0 0 12px 0;
  font-size: 1.5rem;
  color: #333;
  padding: 5px;
  box-sizing: border-box;
}

.restocontent h3 a {
  color: #0066cc;
  text-decoration: none;
  transition: color 0.3s;
  padding: 5px 0px;
  display: inline-block;
  min-width: 44px;
  min-height: 44px;
  box-sizing: border-box;
}

.restocontent h3 a:hover {
  color: #004080;
}

.restolocation {
  display: flex;
  align-items: center;
  color: #007acc;
  margin-bottom: 5px;
  font-size: 1rem;
}

.restolocation img {
  margin-right: 8px;
  color: #007acc;
}

.restocontent p {
  color: #555;
  line-height: 1.6;
  max-height: 6em;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}


/* resto detail */
.resto-detail {
  display: flex;
  flex-wrap: wrap;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.resto-detail-header {
  flex: 1 1 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 20px;
  position: relative; 
}

.resto-title {
  margin-bottom: 20px auto;
  font-size: 24px;
  text-align: center;
}

.resto-image {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
}

.resto-body {
  flex: 2 1 500px;
}

.resto-info {
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}

.resto-info-title {
  margin-bottom: 20px;
  font-size: 24px;
  border-bottom: 2px solid #ccc;
  padding-bottom: 10px;
}

.resto-info-list {
  list-style-type: none;
  padding: 0;
}

.detail-item {
  margin-bottom: 15px;
}

.detail-title {
  font-weight: bold;
  margin-bottom: 5px;
}

.detail-content {
  margin: 0;
}

.reviews {
  background-color: #f9f9f9;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
  max-width: 120em;
  margin-left: auto;
  margin-right: auto;
}

.reviews h2 {
  margin-bottom: 15px;
  font-size: 20px;
  border-bottom: 2px solid #ccc;
  padding-bottom: 8px;
}

.review-item {
  border-bottom: 1px solid #ddd;
  padding-bottom: 10px;
  margin-bottom: 10px;
}

.review-item:last-child {
  border-bottom: none;
  margin-bottom: 0;
  padding-bottom: 0;
}

.review-item p {
  font-size: 14px;
  color: #555;
  line-height: 1.4;
  margin: 6px 0;
}

.review-item p.nama {
  font-weight: bold;
  color: #333;
}

.review-item p.tanggal {
  font-style: italic;
  color: #777;
}

.review-item p.review {
  margin-bottom: 15px;
}



/* favorite */
.favorite {
  width: 200px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(123, 255, 247);
  background: linear-gradient(175deg, rgba(123, 255, 247, 1) 3%, rgba(252, 252, 252, 1) 48%);
  position: fixed;
  bottom: 16px;
  right: 16px;
  border: 3px solid black;
  font-size: 28px;
  color: red; 
  cursor: pointer;
  border-radius: 10%;
  min-width: 44px;
  min-height: 44px;
}

.favorite span {
  font-weight: bold;
  color: black;
}




/*Footer*/
.footer {
  font-weight: bold;
  background: rgb(209, 255, 251);
  background: linear-gradient(
    130deg,
    rgba(209, 255, 251, 1) 13%,
    rgba(255, 255, 255, 1) 33%,
    rgba(255, 255, 255, 1) 50%,
    rgba(210, 255, 251, 1) 75%
  );
  color: #333333;
  padding: 10px;
  text-align: center;
  z-index: 1004;
}



/* Responsive Design */
@media (max-width: 1024px) {
  .hamburger {
    display: inline-block;
  }
  
  .sidebar-container {
    
    display: none;
  }

  .sidebar-container.open {
    display: grid;
    right: 0;
  }
}

@media (min-width: 1025px) {
  .hamburger {
    display: none;
  }

  .sidebar-container {
    display: flex;
    position: static;
    background: transparent; 
    width: auto;
    padding: 0;
    padding-top: -1em; 
    margin-bottom: -2em;
  }

  .sidebar-container ul {
    display: flex;
    flex-direction: row;
  }

  .sidebar-container ul li {
    margin-bottom: 0;
    margin-right: 20px;
  }

  .sidebar-container ul li a {
    padding: 0;
  }
}

@media (max-width: 768px) {
  .homeresto {
    min-height: 280px;
  }
  .resto-name a {
    font-size: 1.25rem;
  }

  .homeresto-title {
    font-size: 5em;
  }

  .homeresto-description {
    font-size: 1.5em;
  }

  .restaurant-list {
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  }

  .resto-img {
    height: 200px;
  }

  .resto-detail-header {
    margin-right: 0;
  }

  .resto-body {
    flex: 1 1 100%;
  }

  .favorite {
    width: 100px;
    height: 25.5px;
    font-size: 14px;
    min-width: 44px;
    min-height: 44px;
  }

  .homeresto-list-title {
    font-size: 25px;
    margin-bottom: -3em;
  }

  .reviews {
    max-width: 45em;
  }

}

@media only screen and (max-width: 650px) {
  .homeresto {
      background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  }
}

@media only screen and (min-width: 651px) {
  .homeresto {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  }
}
