r\n.change .hamburger1 {\r\n  -webkit-transform: rotate(-45deg) translate(-9px, 6px);\r\n  transform: rotate(-45deg) translate(-9px, 6px);\r\n  background-color: #bf1722;\r\n}\r\n\r\n.change .hamburger2 {\r\n  opacity: 0;\r\n  background-color: #bf1722;\r\n}\r\n\r\n.change .hamburger3 {\r\n  -webkit-transform: rotate(45deg) translate(-8px, -8px);\r\n  transform: rotate(45deg) translate(-8px, -8px);\r\n  background-color: #bf1722;\r\n}\r\n\r\n\r\n.restonav {\r\n  padding: 10px 20px;\r\n  box-shadow: 1px 2px 10px 2px rgba(0, 0, 0, 0.1);\r\n  background: linear-gradient(130deg, rgba(209,255,251,1) 13%, rgba(255,255,255,1) 50%, rgba(210,255,251,1) 75%);\r\n  z-index: 99;\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  position: sticky;\r\n  top: 0;\r\n}\r\n\r\n.resto-name a {\r\n  font-family: \"Labrada\", sans-serif;\r\n  font-size: 1.5rem;\r\n  text-decoration: none;\r\n  font-weight: 550;\r\n  color: #333333;\r\n  letter-spacing: 2px;\r\n  padding: 0.5rem 1.5rem;\r\n}\r\n\r\n.sidebar-container {\r\n  position: fixed;\r\n  top: 0;\r\n  bottom: 0;\r\n  right: -300px;\r\n  width: 300px;\r\n  background: linear-gradient(130deg, rgba(230,255,253,1) 23%, rgba(255,251,251,1) 50%, rgba(239,255,253,1) 100%);\r\n  padding: 16px;\r\n  overflow-y: auto;\r\n  transition: all 0.5 ease-in-out;\r\n  display: grid;\r\n  flex-direction: column;\r\n  gap: 10px;\r\n  z-index: 1;\r\n}\r\n\r\n.sidebar-container.open {\r\n  right: 0;\r\n}\r\n\r\n.sidebar-container ul {\r\n  list-style: none;\r\n  padding: 0;\r\n}\r\n\r\n.sidebar-container ul li {\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.sidebar-container ul li a {\r\n  text-decoration: none;\r\n  font-size: 1.1rem;\r\n  font-weight: 500;\r\n  color: #333333;\r\n  padding: 10px 15px;\r\n  border-radius: 999px;\r\n  display: inline-block;\r\n  width: 100%;\r\n  min-width: 44px;\r\n  min-height: 44px;\r\n}\r\n\r\n.sidebar-container ul li:hover a {\r\n  font-weight: bold;\r\n  color: black;\r\n}\r\n\r\n/*homeviews style*/\r\n.homeresto {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: center;\r\n  min-height: 380px;\r\n  width: 100%;\r\n  text-align: center;\r\n  background-image: url(\"../public/images/hero-image_2.jpg\");\r\n  background-size: cover;\r\n  background-position: center;\r\n  background-color: green;\r\n  position: relative;\r\n}\r\n\r\n.homeresto:after {\r\n  content: \"\";\r\n  display: block;\r\n  position: absolute;\r\n  top: 0;\r\n  left: 0;\r\n  right: 0;\r\n  bottom: 0;\r\n  background-color: rgba(239, 231, 230, 0.3);\r\n}\r\n\r\n.homeresto-title {\r\n  font-size: 20em; \r\n  font-weight: 750;\r\n  color: rgba(255, 255, 255, 0.7);\r\n  font-family: serif;\r\n  z-index: 1;\r\n  padding: 20px 25px;\r\n}\r\n\r\n.homeresto-description {\r\n  font-size: 2em;\r\n  color: rgba(255, 255, 255, 0.7);\r\n  font-family: serif;\r\n  z-index: 1;\r\n  padding: 0 25px;\r\n  margin-top: -20px;\r\n  text-transform: uppercase;\r\n  text-decoration: underline;\r\n}\r\n\r\n.homeresto-list-tile {\r\n  margin: 0 auto;\r\n  min-height: 100%;\r\n  background-color:  rgba(182, 255, 245, 0.7);\r\n  color: #333;\r\n  font-family: \"Labrada\", sans-serif;\r\n  font-size: 30px;\r\n  text-align: center;\r\n  position: relative;\r\n  padding: 0 auto;\r\n  margin-bottom: -1.5em;\r\n  border: 2px solid #333;\r\n  border-radius: 10px;\r\n  border-bottom: none;\r\n  z-index: 3;\r\n}\r\n\r\n.restaurant-list {\r\n  display: grid;\r\n  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));\r\n  gap: 15px;\r\n  padding: 2em;\r\n  margin-top: 2em;\r\n}\r\n\r\n\r\n/* resto list */\r\n.restocard {\r\n  width: 100%;\r\n  background: #fffaf0;\r\n  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);\r\n  border-radius: 12px;\r\n  overflow: hidden;\r\n  transition: transform 0.3s;\r\n}\r\n\r\n.restocard:hover {\r\n  transform: scale(1.02);\r\n}\r\n\r\n.restohead {\r\n  position: relative;\r\n}\r\n\r\n.resto-img {\r\n  width: 100%;\r\n  height: 